.font-light{
    font-weight: 300;
}
.font-regular{
    font-weight: 400;
}
.font-semibold{
    font-weight: 600;
}
.font-bold{
    font-weight: 700;
}
.red{
    color:$color-red;
}
.white{
    color:$color-white;
}

html{
    font-family: 'Source Sans Pro', sans-serif;
}
body{
    @extend .font-light;
    font-size: 16px;
    color:$color-black;
}

h1, .header-1{
    @extend .font-light;
    font-size: 18px;
    color:$color-red;
    margin-top: 10px;
    margin-bottom: 5px;
    @media #{$small}{
        font-size: 34px;
     }
}
.header-2{
    @extend .header-1;
    font-size: 16px;
    @media #{$small}{
        font-size: 20px;
     }
}
